<template>
  <div class="sidebar sub-menu-wrap">
    <el-radio-group v-model="isCollapse">
      <el-radio-button :label="true" id="btn-arrow-left" v-show="isShowLeft" @click.native="showRightBtn">
        <i class="el-icon-d-arrow-left"></i>
      </el-radio-button>
      <el-radio-button :label="false" id="btn-arrow-right" v-show="isShowRight" @click.native="showLeftBtn">
        <i class="el-icon-d-arrow-right"></i>
      </el-radio-button>
    </el-radio-group>
    <el-menu class="sidebar-el-menu" @open="handleOpen" @close="handleClose" :default-active="activeIndex" :collapse="isCollapse" background-color="#D8E5F1" text-color="#101010" active-text-color="#fff" unique-opened router>
      <template v-for="item in treeData">
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{ item.title }}</span>
            </template>
            <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index">
              {{ subItem.title }}
            </el-menu-item>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import bus from "../common/bus";
export default {
  data() {
    return {
      collapse: false,

      items: [
        {
          icon: "el-icon-dd",
          index: "costModel",
          title: "能源成本产出模型"
        },
        {
          icon: "el-icon-dn",
          index: "dashboard1",
          title: "多能协同能量流",
          subs: [
            {
              index: "dashboard1",
              title: "多能负荷预测"
            },
            {
              index: "PVview",
              title: "光伏功率预测"
            },
            {
              index: "dashboard1",
              title: "风功率预测"
            },
            {
              index: "dashboard1",
              title: "气象信息"
            }
          ]
        },
        {
          icon: "el-icon-nyjl",
          index: "dashboard7",
          title: "能源计量统计"
        },
        {
          icon: "el-icon-xy",
          index: "dashboard8",
          title: "负荷特性分析"
        },
        {
          icon: "el-icon-nyzl",
          index: "dashboard5",
          title: "能源质量分析"
        },
        {
          icon: "el-icon-yncb",
          index: "dashboard6",
          title: "用能成本分析"
        },
        {
          icon: "el-icon-yx",
          index: "dashboard9",
          title: "负荷转换潜力分析"
        }
      ],
      activeIndex: "costModel",
      isCollapse: false,
      isShowLeft: true,
      isShowRight: false,
      childValue: "我是子组件的数据"
    };
  },
   // 接受父组件的值
   props: {
      treeData: {
      type: Array,
      required: true, // 必需且是bool类型的
      twoWay: true // 双向绑定
    }
   },
  mounted() {},
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    showRightBtn() {
      this.isShowLeft = false;
      this.isShowRight = true;
      this.$emit("childByValue", this.isShowRight);
    },
    showLeftBtn() {
      this.isShowLeft = true;
      this.isShowRight = false;
      this.$emit("childByValue", this.isShowRight);
    }
  },
  computed: {
    onRoutes() {
      return this.$route.path.replace("/", "");
    }
  },
  created() {
    bus.$on("menuitem", text => {
      this.items = text;
    });
    //    this.items=listitems;
    // 通过 Event Bus 进行组件间通信，来折叠侧边栏
    bus.$on("collapse", msg => {
      this.collapse = msg;
    });
  }
};
</script>

<style scoped>
/*#btn-arrow-left{*/
/*display: none;*/
/*}*/
/*#btn-arrow-right{*/
/*display: none;*/
/*}*/
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 69px;
  bottom: 0;
  overflow-y: auto;
  background-color: rgb(216, 229, 241);
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 200px;
}
.sidebar > ul {
  height: 100%;
}


</style>
<style>
  .sidebar .el-menu-item,.sidebar .el-submenu__title{height:40px;line-height:40px;}
.sidebar .el-submenu .el-menu-item{height:30px;line-height:30px;}
</style>



