<template>
  <div class="header headbox" >

    <div class="logo left">
      <img src="../../assets/img/logo.png" class="logo-img">
    </div>
    <h6 class="main-title left">珠海多能协同运行管理平台</h6>
    <div class="left main-menu-wrap">
      <el-menu
        :default-active="activeIndex"
        :router="true"
        class="el-menu-demo main-menu"
        mode="horizontal"
        background-color="#1567B3"
        text-color="#fff"
        active-text-color="#fff">
        <el-menu-item index="1" :route="{path: '/constructionPan'}">能源网总览</el-menu-item>
        <el-menu-item index="2" :route="{path: '/oversee/emsWebDashborad'}">运行监视</el-menu-item>
        <el-menu-item index="3" :route="{path: '/analysis/inoutWebModel'}"> 运行分析</el-menu-item>
        <el-menu-item index="4" :route="{path: '/optimize/loadWebForecast'}">多能优化</el-menu-item>
        <el-menu-item index="5" :route="{path: '/resource/runWebAnalysis'}">分布式资源管理</el-menu-item>
        <el-menu-item index="6" :route="{path: '/setting/devManage'}">系统设置</el-menu-item>
        </el-menu>
    </div>
    <div class="right user-wrap">
      <div class="user-name left">
        <img src="../../assets/img/people.png" alt="" class="user-img">
        <span class="name-text">李晓明</span>
      </div>
      <div class="log-out left" @click="logout">退出</div>
    </div>

     <!-- 折叠按钮 -->
    <!--<div class="collapse-btn" @click="collapseChage">-->
            <!--<i class="el-icon-menu"></i>-->
        <!--</div>-->
    <!--<div class="header-right">-->
      <!--<div class="header-user-con">-->
        <!--&lt;!&ndash; 全屏显示 &ndash;&gt;-->
        <!--<div class="btn-fullscreen" @click="handleFullScreen">-->
          <!--<el-tooltip effect="dark" :content="fullscreen?`取消全屏`:`全屏`" placement="bottom">-->
            <!--<i class="el-icon-rank"></i>-->
          <!--</el-tooltip>-->
        <!--</div>-->

      <!--</div>-->
    <!--</div>-->
  </div>
</template>
<script>
import Vue from 'vue';
import bus from "../common/bus";
export default {
  data() {
    return {
      collapse: false,
      fullscreen: false,
      name: "linxin",
      message: 2,
      activeIndex: '3',
    };
  },
  computed: {
    username() {
      let username = localStorage.getItem("ms_username");
      return username ? username : this.name;
    }
  },
  methods: {

    // 用户名下拉菜单选择事件
    handleCommand(command) {
      if (command == "loginout") {
        localStorage.removeItem("ms_username");
        this.$router.push("/login");
      }
    },
    // 侧边栏折叠
    collapseChage() {
      this.collapse = !this.collapse;
      bus.$emit("collapse", this.collapse);
    },
    // 全屏事件
    handleFullScreen() {
      let element = document.documentElement;
      if (this.fullscreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        if (element.requestFullscreen) {
          element.requestFullscreen();
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen();
        }
      }
      this.fullscreen = !this.fullscreen;
    },
    logout(){//退出，跳到登录页
        Vue.deleteCookie('userinfo');
        location.href="/login";
    },
  },
  mounted() {
       let that=this;
       bus.$on("menuitem", flag => {
       //that.activeIndex=flag;
    });
    if (document.body.clientWidth < 1500) {
      this.collapseChage();
    }
  }
};
</script>
<style scoped>
.collapse-btn {
  float: left;
  padding: 0 21px;
  cursor: pointer;
  line-height: 70px;
}
.header .logo {
  float: left;
  width: 200px;
  line-height: 50px;
}
.header-right {
  float: right;
  padding-right: 50px;
}
.header-user-con {
  display: flex;
  height: 70px;
  align-items: center;
}
.btn-fullscreen {
  transform: rotate(45deg);
  margin-right: 5px;
  font-size: 24px;
}
.btn-bell,
.btn-fullscreen {
  position: relative;
  width: 30px;
  height: 30px;
  text-align: center;
  border-radius: 15px;
  cursor: pointer;
}
.btn-bell-badge {
  position: absolute;
  right: 0;
  top: -2px;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background: #f56c6c;
  color: #fff;
}
.btn-bell .el-icon-bell {
  color: #fff;
}

.user-avator {
  margin-left: 20px;
}
.user-avator img {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-dropdown-link {
  color: #fff;
  cursor: pointer;
}
.el-dropdown-menu__item {
  text-align: center;
}
.sysName {
  float: left;
  font-size: 14px;
  font-family: "PingFang SC, Heavy";
  line-height: 60px;
}
</style>
<style>
.headbox.header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 50px;
  font-size: 22px;
  color: #fff;
}
.headbox .main-menu-wrap{width:55%;}
.headbox .logo-img{margin-top:7px;}
.headbox .main-title{line-height:50px;}
.headbox .main-menu-wrap .el-menu--horizontal>.el-menu-item{height:50px;line-height:50px;}
.headbox .user-name{line-height:50px;}
.headbox .user-img{margin-top:15px;}
.headbox .log-out{line-height:50px;cursor:pointer;}
.headbox .el-menu--horizontal{border-bottom:0;}
.headbox .el-menu-item a{display:block;line-height:50px;text-align:center;}

</style>



